<script>
import $ from 'jquery'
import breakpoints from '../../static/js/breakpoints.min'
import ExpertProfile from './ExpertProfile'
import HottestExpert from './HottestExpert'
import Menu from './Menu'

export default {
  name: 'Index',
  data () {
    return {
      indexPageData: {}, // 进入首页时从后台得到的数据
      expertQueryList: [], // 查询专家时从后台得到的数据
      queryCondition: {}, // 记录当前的查询条件
      expertQueryListPageTotal: 0, // 查询专家结果-总页数
      expertQueryListPageCurrent: 0, // 查询专家结果-当前页码
      activeHottestExpert: {}, // 激活态的热门专家类别
      showMenu: false // 是否展示菜单栏
    }
  },
  // 监听, 当路由发生变化的时候执行
  watch: {
    'showMenu': 'showMenuChange'
  },
  components: {
    'expert-profile': ExpertProfile,
    'hottest-expert': HottestExpert,
    'menu': Menu
  },
  // 组件生命周期钩子
  beforeCreate () {
    // console.log('Index.vue beforeCreate 创建前状态 ===============>')
    // console.log(this.$el)
  },
  created () {
    // console.log('Index.vue created 创建完毕状态 ===============>')
    // 调用 store 的 Action：异步 axios 获取数据
    // this.$store.dispatch('getIndexPageDataAction')
  },
  beforeMount () {
    // console.log('Index.vue beforeMount 挂载前状态 ===============>')
    // console.log(this.$el)
  },
  mounted () {
    // console.log('Index.vue mounted 挂载完毕状态 ===============>')
    // console.log(this.$el)

    // Vue 数据初始化
    this.indexPageData = {}
    this.expertQueryList = []
    this.queryCondition = {}
    this.expertQueryListPageTotal = 0
    this.expertQueryListPageCurrent = 0
    this.activeHottestExpert = {
      class1: 'A', // 第一类 A B C D E
      class2: 'F', // 第二类 F G H I J
      class3: 'K', // 第三类 K N O P Q
      class4: 'R', // 第四类 R S T
      class5: 'U' // 第五类 U V X Z
    }
    this.showMenu = false

    // AJAX 获得数据
    this.loadHomePage()
    this.init()
  },
  beforeUpdate () {
    // console.log('Index.vue beforeUpdate 更新前状态 ===============>')
    // console.log(this.$el)
  },
  updated () {
    // console.log('Index.vue updated 更新完毕状态 ===============>')
    // console.log(this.$el)
  },
  beforeDestroy () {
    // console.log('Index.vue beforeDestroy 销毁前状态 ===============>')
    // console.log(this.$el)
  },
  destroyed () {
    // console.log('Index.vue destroyed 销毁完毕状态 ===============>')
    // console.log(this.$el)
  },
  methods: {
    // 监听 this.showMenu ，发生变化的时候执行
    showMenuChange () {
      // console.log(this.showMenu)
    },
    // 切换显示菜单栏
    toggleShowMenu () {
      // console.log('toggleShowMenu () ', this.showMenu)
      this.showMenu = !this.showMenu

      // let _this = this
      let jqMainModule = $('article.main-module')
      if (this.showMenu) {
        jqMainModule
          .css({'opacity': '0.4'})
        // .click(_this.toggleShowMenu) // 点击主题区域则退出菜单栏
      } else {
        jqMainModule
          .css({'opacity': '1'})
        // .click(function () { // 停止事件响应
        //   event.stopPropagation()
        // })
      }
    },
    // TODO 用户登录
    login () {
      'use strict'

      let jqUsername = $('input.username')
      let jqPassword = $('input.password')
      let jqLogin = $('section.menu a.login')
      let jqRegister = $('section.menu a.register')

      let username = jqUsername.val().trim()
      let password = jqPassword.val().trim()
      let choice = jqLogin.text()

      if (choice === '注销') {
        if (confirm('确认注销')) {
          jqUsername.show()
          jqPassword.show()
          jqLogin.text('登录')
          // 让注册按钮可选
          if (jqRegister.hasClass('disabled')) {
            jqRegister.removeClass('disabled')
          }
          // 修改登录状态
          if (this.$store.state.loginFlag) {
            this.$store.dispatch('changeLoginFlag', false)
          }
        }
      } else if (choice === '登录') {
        if (!username || username === '') {
          console.log('请您输入账号！')
          jqUsername.focus()
        } else if (!password || password === '') {
          console.log('请您输入密码！')
          jqPassword.focus()
        } else {
          if (username === 'yinyuwei' && password === '12345678') {
            // TODO login 判断逻辑交由后台处理
            jqUsername.hide()
            jqPassword.hide()
            jqLogin.text('注销')
            // 让注册按钮不可选
            if (!jqRegister.hasClass('disabled')) {
              jqRegister.addClass('disabled')
            }
            // 修改登录状态
            if (!this.$store.state.loginFlag) {
              this.$store.dispatch('changeLoginFlag', true)
            }
            console.log('登录成功！')
          } else {
            console.log('登录失败！')
          }
        }
      } else {
        console.error('choice error !!!')
      }
    },
    // TODO 用户注册
    register () {
      'use strict'

      let jqUsername = $('input.username')
      let jqPassword = $('input.password')
      let jqLogin = $('section.menu a.login')
      let jqRegister = $('section.menu a.register')

      let username = jqUsername.val().trim()
      let password = jqPassword.val().trim()

      if (!username || username === '') {
        console.log('请您输入账号！')
        jqUsername.focus()
      } else if (!password || password === '') {
        console.log('请您输入密码！')
        jqPassword.focus()
      } else {
        // 做基本的输入格式判断，后端要做更细致的判断
        let usernamePattern = /^[a-zA-Z_][0-9a-zA-Z_]+$/ // 由数字、英文字母和下划线组成，且不能以数字开头
        let passwordPattern = /^[\S]+$/ // \S 匹配任何非空白字符, 等价于 [^\f\n\r\t\v]
        if (username.length < 6) {
          console.log('用户名至少应有6位字符！')
          jqUsername.focus()
        } else if (username.length > 16) {
          console.log('用户名不应超过16位字符！')
          jqUsername.focus()
        } else if (password.length < 8) {
          console.log('密码至少应有8位字符！')
          jqPassword.focus()
        } else if (password.length > 18) {
          console.log('密码不应超过18位字符！')
          jqUsername.focus()
        } else if (password.indexOf(username) !== -1) {
          console.log('密码中不应包含您的用户名！')
          jqUsername.focus()
        } else if (!usernamePattern.test(username)) {
          console.log('用户名只能由数字、英文字母和下划线组成，且不能以数字开头！')
          jqUsername.focus()
        } else if (!passwordPattern.test(password)) {
          console.log('密码中不能包含空白字符！')
          jqPassword.focus()
        } else if (username === 'yinyuwei' && password === '12345678') {
          // TODO register 判断逻辑交由后台处理
          jqUsername.hide()
          jqPassword.hide()
          jqLogin.text('注销')
          // 让注册按钮不可选
          if (!jqRegister.hasClass('disabled')) {
            jqRegister.addClass('disabled')
          }
          // 修改登录状态
          if (!this.$store.state.loginFlag) {
            this.$store.dispatch('changeLoginFlag', true)
          }
          console.log('注册成功！')
        } else {
          console.log('注册失败！')
        }
      }
    },
    // AJAX 获得数据，成功后渲染首页
    loadHomePage: function () {
      'use strict'
      let _this = this

      let startDate = new Date()
      let endDate

      let serverUrl = '/'

      // HTTP GET (JQuery Ajax)
      $.ajax({
        type: 'GET',
        url: serverUrl,
        dataType: 'json',
        // async: true, // 使用同步操作
        // timeout: 10000, // 10秒超时时间
        beforeSend: function (xhr) {
          endDate = new Date()
          console.log(endDate - startDate)

          console.log('$.ajax beforeSend, state = ' + xhr.status)
          // 出现loading动画
          $('div.load-container').css({'display': 'block'})
        },
        success: function (result, status, xhr) {
          endDate = new Date()
          console.log(endDate - startDate)

          console.log('$.ajax success, state = ' + xhr.status)
          // 隐藏loading动画
          $('div.load-container').css({'display': 'none'})

          if (!result || result === '' || result.length === 0) {
            // TODO
            $.toast({
              text: '抱歉，该专家无信息！',
              showHideTransition: 'plain',
              bgColor: 'red',
              textColor: '#e0e0e0',
              allowToastClose: false,
              hideAfter: 2400,
              stack: 5,
              textAlign: 'center',
              position: 'mid-center'
            })
            console.log('抱歉，该专家无信息。')
            return false
          }

          // 接收数据初始化
          _this.indexPageData = {}

          // 解析JSON数据
          _this.indexPageData.ccn_experts = JSON.parse(result.ccn_experts)
          _this.indexPageData.hottest_five_organizations = []
          _this.indexPageData.hottest_five_experts = []

          let ccnExpertsTemp = _this.indexPageData.ccn_experts
          ccnExpertsTemp.forEach(function (ccnExpertsItem, ccnExpertsIndex) {
            ccnExpertsItem.experts.forEach(function (expertItem, index) {
              _this.indexPageData.ccn_experts[ccnExpertsIndex].experts[index] = JSON.parse(expertItem)
            })
          })

          let hottestFiveOrganizationsTemp = JSON.parse(result.hottest_five_organizations)
          hottestFiveOrganizationsTemp.forEach(function (item, index) {
            _this.indexPageData.hottest_five_organizations.push(JSON.parse(item))
          })

          let hottestFiveExpertsTemp = JSON.parse(result.hottest_five_experts)
          hottestFiveExpertsTemp.forEach(function (item, index) {
            _this.indexPageData.hottest_five_experts.push(JSON.parse(item))
          })

          // console.log(_this.indexPageData.hottest_five_organizations)
