          'class="advanced-input" value=""  placeholder="请输入专家学者姓名" /></span>' +
          '<span class="input-right"><p>所属机构:</p><input type="text" id="organization-input" ' +
          'name="organization-input" class="advanced-input" value=""  placeholder="请输入学者所属机构" /></span><br />' +
          '<span class="input-left"><p>钻研领域:</p><input type="text" id="field-input" name="field-input" ' +
