          '<span class="input-right"><p>研究内容:</p><input type="text" id="research-content-input" ' +
          'name="research-content-input" class="advanced-input" value="" placeholder="请输入学者研究内容" /></span>'
        )
        $('#advanced-input-list').css({'opacity': '0'})
          .append(html)
          .animate({opacity: 1}, 500)
        // input框获取焦点时清除placeholder
        $('input#researcher-input').focus(function () {
          $('input#researcher-input').attr({'placeholder': ''})
        }).blur(function () {
          $('input#researcher-input').attr({'placeholder': '请输入专家学者姓名'})
        })

        $('input#organization-input').focus(function () {
          $('input#organization-input').attr({'placeholder': ''})
        }).blur(function () {
          $('input#organization-input').attr({'placeholder': '请输入学者所属机构'})
